import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';

/**
 * Der LuxStorageService speichert Daten im lokalen Browserstorage.
 * Wenn man beim Speichern (Methode -> setItem) angibt, dass es sich um sensible Daten handelt,
 * knnen diese einfach ber die Methode 'clearSensitiveItems' gelscht werden.
 */
@Injectable()
export class LuxStorageService {
  private readonly postfixSensitive = '.sensitive';

  private itemSources: Map<string, BehaviorSubject<string>> = new Map();

  constructor() {
    addEventListener('storage', (event: StorageEvent) => {
      if (event.key) {
        if (this.itemSources.has(event.key)) {
          this.itemSources.get(event.key).next(event.newValue);
        }
      }
    });
  }

  /**
   * Diese Methode liefert den Wert fr den bergebenen Schlssel zurck.
   *
   * @param key - Der eindeutige Schlssel.
   * @returns Liefert den Wert fr den bergebenen Schlssel zurck.
   */
  getItem(key: string): string {
    if (!this.itemSources.has(key)) {
      this.itemSources.set(key, new BehaviorSubject<string>(localStorage.getItem(key)));
    }

    return this.itemSources.get(key).getValue();
  }

  /**
   * Diese Methode liefert ein Observable zurck, das ber alle nderungen an dem Schlssel informiert wird.
   *
   * @param key - Der eindeutige Schlssel.
   * @returns Liefert ein Observable zurck, das ber alle nderungen an dem Schlssel informiert wird.
   */
  getItemAsObservable(key: string): Observable<string> {
    if (!this.itemSources.has(key)) {
      this.itemSources.set(key, new BehaviorSubject<string>(localStorage.getItem(key)));
    }

    return this.itemSources.get(key).asObservable();
  }

  /**
   * Diese Methode setzt den bergebenen Wert fr den Schlssel. Zustzlich muss angegeben werden, ob es sich um
   * sensible oder personenbezogene Daten handelt.
   *
   * @param key - Der eindeutige Schlssel.
   * @param value - Der neue Wert.
   * @param sensitive - Gibt an, ob es sich um sensible oder personenbezogene Daten handelt. Diese knnen leicht ber
   *                    die Methode 'clearSensitiveItems' (z.B. beim Ausloggen) gelscht werden.
   */
  setItem(key: string, value: string, sensitive: boolean): void {
    try {
      localStorage.setItem(key, value);

      if (sensitive) {
        localStorage.setItem(key + this.postfixSensitive, 'true');
      }
      if (this.itemSources.has(key)) {
        this.itemSources.get(key).next(localStorage.getItem(key));
      }
    } catch (error) {
      this.itemSources.get(key).error(error);
    }
  }

  /**
   * Diese Methode entfernt den bergebenen Schlssel.
   *
   * @param key - Der eindeutige Schlssel.
   */
  removeItem(key: string): void {
    localStorage.removeItem(key);
    localStorage.removeItem(key + this.postfixSensitive);

    if (this.itemSources.has(key)) {
      this.itemSources.get(key).next(localStorage.getItem(key));
    }
  }

  /**
   * Diese Methode lscht alle sensiblen und personenbezogenen Eintrge (d.h. alle Items bei denen das Flag 'sensitive'
   * auf true gesetzt wurde).
   */
  clearSensitiveItems(): void {
    // Alle Schlssel sammeln.
    const keys: string[] = [];
    for (let i = 0; i < localStorage.length; i++) {
      keys.push(localStorage.key(i));
    }

    // Alle sensiblen Eintrge lschen.
    keys.forEach(key => {
      if (key && key.endsWith(this.postfixSensitive)) {
        this.removeItem(key.replace(this.postfixSensitive, ''));
      }
    });
  }

  /**
   * Diese Methode lscht alle Eintrge aus dem Storage.
   */
  clearAll(): void {
    localStorage.clear();
    this.itemSources.forEach((itemSource: BehaviorSubject<string>) => {
      itemSource.next(null);
      itemSource.complete();
    });

    this.itemSources.clear();
  }
}
